@import url(https://fonts.googleapis.com/css2?family=VT323&display=swap);
@import url(https://fonts.googleapis.com/css2?family=VT323&display=swap);
*{margin:0;padding:0;box-sizing:border-box;outline:0;vertical-align:baseline;font-weight:inherit;font-family:inherit;font-style:inherit;font-size:100%}body{font-family:VT323, sans-serif;background-color:#283745}.App{width:95%;height:100vh;margin:0 auto}.stats-image{text-align:center;margin:15px 0}.stats-image img{width:100%;max-width:80%}

.button-home{border:0.5px solid white;width:100px;margin:5px auto 15px auto;border-radius:10px;background-color:white;text-align:center}.button-home a{text-decoration:none;margin-bottom:10px;width:100px;color:black}.stat-name{color:white;font-size:3em;font-weight:bold;text-align:center}.wrapper{margin:1em auto;display:inline-block;align-items:center}.wrapper .inner{margin:0 auto;border-radius:10px;border:0.5px solid black;padding:10px;height:auto;width:340px;background:linear-gradient(15deg, #dd580a 64%, #bd6328 70%, #d3561d 81%, #f05946 86%, #962f07 89%, #c2ab60 100%)}.wrapper .inner *{margin-bottom:8px}.wrapper .inner .stat-type{display:flex;align-items:center}.wrapper .inner .stat-type h3{margin-left:10px}.wrapper .inner .stat-type .type0{background-color:red;padding:3px;border:0.5px solid gray;border-radius:10px}.wrapper .inner .stat-type .type1{background-color:blue;padding:3px;border:0.5px solid gray;border-radius:10px}.wrapper .inner .stat-ability{display:flex;align-items:center}.wrapper .inner .stat-ability h3{margin-left:10px}.wrapper .inner .stat-ability .ability1{background-color:red;padding:3px;border:0.5px solid gray;border-radius:10px}.wrapper .inner .stat-ability .ability2{background-color:blue;padding:3px;border:0.5px solid gray;border-radius:10px}.wrapper .inner .description{margin:15px 0;text-align:center;border:0.5px solid black;border-radius:5px;box-sizing:border-box;background:linear-gradient(15deg, #cad5b5 64%, #dde2d4 70%, #dde2d4 81%, #fff 86%, #dde2d4 89%, #dde2d4 100%)}.wrapper .inner .description h3{padding:10px}.wrapper .inner2{margin:0 auto;border:0.5px solid black;text-align:center;height:400px;width:310px;max-width:310px;border-radius:10px;background:linear-gradient(15deg, #dd580a 64%, #bd6328 70%, #d3561d 81%, #f05946 86%, #962f07 89%, #c2ab60 100%)}.wrapper .inner2 h2{margin-bottom:10px}.wrapper .inner2>* div{margin-bottom:10px}.wrapper .inner2:hover{-webkit-transform:rotate(0);transform:rotate(0)}.loading{text-align:center;margin-top:15px;font-size:40px;color:white}@media screen and (min-width: 767px){.wrapper{-webkit-perspective:1000px;perspective:1000px;margin:3em 1em;display:flex;flex-direction:row;justify-content:center;align-items:center}.wrapper .inner{margin:10px 50px 50px 10px;transition:0.3s;-webkit-transform:rotateY(40deg);transform:rotateY(40deg);width:300px;height:auto}.wrapper .inner:hover{-webkit-transform:rotate(0);transform:rotate(0)}.wrapper .inner2{margin:40px 50px 50px 50px;transition:0.3s;-webkit-transform:rotateY(-40deg);transform:rotateY(-40deg);width:300px;height:400px}.wrapper .inner2:hover{-webkit-transform:rotate(0);transform:rotate(0)}}

.pokemon-list-items{width:100%;display:flex;align-items:center;flex-wrap:wrap;justify-content:space-evenly}.pokemon-list-items .pokemon-list-link{text-decoration:none !important;color:inherit !important;margin:0.7rem 0}.pokemon-list-items .pokemon-list-link .pokemon-list-card{background-color:rgba(250,0,0,0.7);border:1px solid black;border-radius:5% 20%;font-size:1.7em;width:320px;position:relative}.pokemon-list-items .pokemon-list-link .pokemon-list-card>div{margin:20px 3px 25px 30px}.pokemon-list-items .pokemon-list-link .pokemon-list-card>div h2{font-weight:600}.pokemon-list-items .pokemon-list-link .pokemon-list-card>div .pokemon-list-card-image-container{margin:15px 0;text-align:center;width:90%;border:0.5px solid black;border-radius:5px;box-sizing:border-box;background:linear-gradient(15deg, #cad5b5 64%, #dde2d4 70%, #dde2d4 81%, #fff 86%, #dde2d4 89%, #dde2d4 100%)}.pokemon-list-items .pokemon-list-link .pokemon-list-card>div .pokemon-list-card-image-container img{width:180px;text-align:center}@media (min-width: 767px){.pokemon-list-items{flex-direction:row;flex-wrap:wrap}.pokemon-list-items .pokekon-list-link{margin:10px 20px !important}}

.textField{display:flex !important;margin:0 auto !important;margin:5px auto 30px auto !important;max-width:400px !important;background-color:white !important;border-radius:10px !important}.textField label{font-family:VT323, sans-serif !important}.textField div>input{font-family:VT323, sans-serif !important}.types-filter{display:flex;flex-direction:row;flex-wrap:wrap;justify-content:center;text-align:center;margin-bottom:20px}.types-filter button{width:auto;padding:8px 10px;border-radius:2em;margin:0 5px 8px 5px;border:0.2px solid black;cursor:pointer}.types-filter button:hover{border:2px solid black !important}.types-filter button:active{color:red}

.searchContainer{text-align:center;margin:20px 0}.searchContainer img{width:100%;max-width:600px}

