.button-home {
  border: 0.5px solid white;
  width: 100px;
  margin: 5px auto 15px auto;
  border-radius: 10px;
  background-color: white;
  text-align: center;

  a {
    text-decoration: none;
    margin-bottom: 10px;
    width: 100px;
    color: black;
  }
}

.stat-name {
  color: white;
  font-size: 3em;
  font-weight: bold;
  text-align: center;
}

.wrapper {
  margin: 1em auto;
  display: inline-block;
  align-items: center;

  .inner {
    margin: 0 auto;
    border-radius: 10px;
    border: 0.5px solid black;
    padding: 10px;
    height: auto;
    width: 340px;
    background:
      linear-gradient(
        15deg,
        #dd580a 64%,
        #bd6328 70%,
        #d3561d 81%,
        rgb(240, 89, 70) 86%,
        #962f07 89%,
        #c2ab60 100%
      );

    * {
      margin-bottom: 8px;
    }

    .stat-type {
      display: flex;
      align-items: center;

      h3 {
        margin-left: 10px;
      }

      .type0 {
        background-color: red;
        padding: 3px;
        border: 0.5px solid gray;
        border-radius: 10px;
      }

      .type1 {
        background-color: blue;
        padding: 3px;
        border: 0.5px solid gray;
        border-radius: 10px;
      }
    }

    .stat-ability {
      display: flex;
      align-items: center;

      h3 {
        margin-left: 10px;
      }

      .ability1 {
        background-color: red;
        padding: 3px;
        border: 0.5px solid gray;
        border-radius: 10px;
      }

      .ability2 {
        background-color: blue;
        padding: 3px;
        border: 0.5px solid gray;
        border-radius: 10px;
      }
    }

    .description {
      margin: 15px 0;
      text-align: center;
      border: 0.5px solid black;
      border-radius: 5px;
      box-sizing: border-box;
      background:
        linear-gradient(
          15deg,
          #cad5b5 64%,
          #dde2d4 70%,
          #dde2d4 81%,
          #fff 86%,
          #dde2d4 89%,
          #dde2d4 100%
        );

      h3 {
        padding: 10px;
      }
    }
  }

  .inner2 {
    margin: 0 auto;
    border: 0.5px solid black;
    text-align: center;
    height: 400px;
    width: 310px;
    max-width: 310px;
    border-radius: 10px;
    background:
      linear-gradient(
        15deg,
        #dd580a 64%,
        #bd6328 70%,
        #d3561d 81%,
        rgb(240, 89, 70) 86%,
        #962f07 89%,
        #c2ab60 100%
      );

    h2 {
      margin-bottom: 10px;
    }

    > * div {
      margin-bottom: 10px;
    }
  }

  .inner2:hover {
    transform: rotate(0);
  }
}
// Language: HTML

.loading {
  text-align: center;
  margin-top: 15px;
  font-size: 40px;
  color: white;
}

@media screen and (min-width: 767px) {
  .wrapper {
    perspective: 1000px;
    // perspective-origin: 75% 70%;
    margin: 3em 1em;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;

    .inner {
      margin: 10px 50px 50px 10px;
      transition: 0.3s;
      transform: rotateY(40deg);
      width: 300px;
      height: auto;
    }

    .inner:hover {
      transform: rotate(0);
    }

    .inner2 {
      margin: 40px 50px 50px 50px;
      transition: 0.3s;
      transform: rotateY(-40deg);
      width: 300px;
      height: 400px;
    }

    .inner2:hover {
      transform: rotate(0);
    }
  }
}
